import React, { Component } from 'react';
import classNames from 'classnames';
import './PropertiesPanel.css';

class PropertiesPanel extends Component {
  
  constructor(props) {
    super(props);

    this.state = {
      showElementProperties: true
    };

    this.togglePropertiesPanel = this.togglePropertiesPanel.bind(this);

    this.elementPropertiesClass = classNames(
      'element-properties',
      {'element-properties-show': this.state.showElementProperties }
    );
  }

  togglePropertiesPanel() {
    this.setState({
        showElementProperties: !this.state.showElementProperties
    });

    this.elementPropertiesClass = classNames(
      'element-properties',
      {'element-properties-show': !this.state.showElementProperties }
    );
  }

  render() {
    return (
      <div className={this.elementPropertiesClass}>
        <div className="element-main-header">
        <span>Header</span> Properties
            <span onClick={this.togglePropertiesPanel} className="glyphicon glyphicon-remove pull-right form-elements-remove"></span>
        </div>
    </div>
    );
  }
}

export default PropertiesPanel;


