import { ADD_FIELD, DELETE_FIELD } from '../constants/ActionTypes'

const initialState = []

export default function field(state = initialState, action) {
  switch (action.type) {
    case ADD_FIELD:
      //return [action.text, ...state]
      return action.text

    case DELETE_FIELD:
      return state.filter(field =>
        field.id !== action.id
      )

    default:
      return state
  }
}


